import { PickerFrame } from "../components/picker-frame";
import { Section } from "../components/section";
import { TokenizedCode } from "../components/tokenized-code";
import { FontStyle, StyleGrade, WeightGrade } from "../data-import/grades";
import * as tokenizedSampleCode from "../tokenized-sample-code";
import { WordNumber } from "../components/word-number";
import { Fragment } from "react";

export function ProportionalSampler() {
    const styleGrades = [StyleGrade.Aile, StyleGrade.Etoile, StyleGrade.Sparkle];

    return (
        <Section>
            <Desc styleGrades={styleGrades} />
            <div className="proportional-sampler-body">
                <PickerFrame
                    styleGrades={styleGrades}
                    disableWidth={true}
                    content={SamplerInner}
                    defaultFontStyle={{ style: StyleGrade.Etoile, weight: WeightGrade.Light }}
                />
            </div>
        </Section>
    );
}

function Desc(props: { styleGrades: StyleGrade[] }) {
    return (
        <div className="section-introduction">
            <h2 className="header">For Every Writing</h2>
            <div className="content">
                <p>
                    The <WordNumber n={props.styleGrades.length} /> quasi-proportional families:{" "}
                    <FamilyNameList styleGrades={props.styleGrades} />, are designed targeting{" "}
                    <em>documentation preparation</em> and <em>writing</em>. They provide more
                    variety on character widths, including a slightly wider letter <code>w</code>
                    &nbsp;and&nbsp;<code>m</code>, and narrower <code>i</code>
                    &nbsp;and&nbsp;<code>l</code>.
                </p>
            </div>
        </div>
    );
}

function FamilyNameList(props: { styleGrades: StyleGrade[] }) {
    return (
        <>
            {props.styleGrades.map((grade, index) => (
                <Fragment key={index}>
                    {index === 0 ? "" : index === props.styleGrades.length - 1 ? ", and " : ", "}
                    <em key={grade}>Iosevka&nbsp;{StyleGrade[grade]}</em>
                </Fragment>
            ))}
        </>
    );
}

function SamplerInner(props: { fontStyle: FontStyle }) {
    return (
        <TokenizedCode code={tokenizedSampleCode.Novel} fontStyle={props.fontStyle} literature />
    );
}
