export type FontStyle = {
    // Font selection (to CSS classes)
    style?: null | undefined | StyleGrade;
    weight?: null | undefined | WeightGrade;
    width?: null | undefined | WidthGrade;
    slant?: null | undefined | SlantGrade;

    // OT
    spacingTag?: null | undefined | string;
    ligationTag?: null | undefined | string;
    ligationOn?: null | undefined | boolean;
    charVarTags?: null | undefined | string[];
};

export enum StyleGrade {
    Sans = 1,
    Slab = 2,
    Aile = 3,
    Etoile = 4,
    Sparkle = 5,
    FiraCode = 6,
    IbmPlex = 7
}
export enum WeightGrade {
    Thin = 100,
    ExtraLight = 200,
    Light = 300,
    Regular = 400,
    Book = 450,
    Medium = 500,
    SemiBold = 600,
    Bold = 700,
    ExtraBold = 800,
    Heavy = 900
}
export enum WidthGrade {
    Normal = 5,
    Expanded = 7
}
export enum SlantGrade {
    Upright = 1,
    Oblique = 2,
    Italic = 3
}
export const DefaultFontStyle: FontStyle = {
    style: StyleGrade.Sans,
    weight: WeightGrade.Regular,
    width: WidthGrade.Normal,
    slant: SlantGrade.Upright
};
export const DefaultSubtitleStyle: FontStyle = {
    ...DefaultFontStyle,
    weight: WeightGrade.Light
};
export function fontStyleToCls(fs: FontStyle) {
    let classes: string[] = [];
    if (fs.style === StyleGrade.Sans) classes.push("sans");
    if (fs.style === StyleGrade.Slab) classes.push("slab");
    if (fs.style === StyleGrade.Aile) classes.push("aile");
    if (fs.style === StyleGrade.Etoile) classes.push("etoile");
    if (fs.style === StyleGrade.Sparkle) classes.push("sparkle");
    if (fs.style === StyleGrade.FiraCode) classes.push("fira-code");
    if (fs.style === StyleGrade.IbmPlex) classes.push("ibm-plex");
    if (fs.weight) classes.push("w-" + fs.weight);
    if (fs.width) classes.push("wd-" + fs.width);
    if (fs.slant) classes.push("s-" + fs.slant);
    return classes.join(" ");
}
export function fontStyleToOtStyle(fs: FontStyle) {
    let s: string[] = [];
    if (fs.spacingTag && isValidTag(fs.spacingTag)) {
        s.push(`'${fs.spacingTag}' on`);
    }
    if (fs.ligationTag && isValidTag(fs.ligationTag)) {
        if (fs.ligationTag !== "calt") s.push(`'calt' off`);
        s.push(`'${fs.ligationTag}' ${fs.ligationOn ? "on" : "off"}`);
    }
    if (fs.charVarTags) {
        for (const tag of fs.charVarTags) {
            if (isValidTag(tag)) s.push(`'${tag}' on`);
        }
    }
    return { fontFeatureSettings: s.join(", ") };
}
function isValidTag(tag: string) {
    return tag && tag.length === 4;
}

export const DefaultStyleGrade: StyleGrade[] = [StyleGrade.Sans, StyleGrade.Slab];
export const DefaultWeightGrades: WeightGrade[] = [
    WeightGrade.Thin,
    WeightGrade.ExtraLight,
    WeightGrade.Light,
    WeightGrade.Regular,
    WeightGrade.Medium,
    WeightGrade.SemiBold,
    WeightGrade.Bold,
    WeightGrade.ExtraBold,
    WeightGrade.Heavy
];
export const DefaultWidthGrade: WidthGrade[] = [WidthGrade.Normal, WidthGrade.Expanded];
export const DefaultSlantGrade: SlantGrade[] = [
    SlantGrade.Upright,
    SlantGrade.Oblique,
    SlantGrade.Italic
];
