import { createContext, useContext, useState } from "react";
import { PickerFrame } from "../components/picker-frame";
import { Section } from "../components/section";
import { AvailableLigationSets, LigationSamples, LigationSet } from "../data-import";
import {
    DefaultFontStyle,
    FontStyle,
    fontStyleToCls,
    fontStyleToOtStyle
} from "../data-import/grades";
import { joinCls } from "../utils/join-classes";

const ligationOff = AvailableLigationSets[0];
const ligationDefault = AvailableLigationSets[1];

type LigationSamplerContextProps = {
    currentLigationSet: LigationSet;
    availableLigations: LigationSet[];
};
const LigationSampleContext = createContext<LigationSamplerContextProps>({
    currentLigationSet: ligationOff,
    availableLigations: []
});

export function LigationSampler() {
    const [currentLigationSet, setCurrentLigationSet] = useState<LigationSet>(ligationDefault);

    const ctxProps: LigationSamplerContextProps = {
        currentLigationSet,
        availableLigations: AvailableLigationSets
    };

    const receiver = (ls: LigationSet) => setCurrentLigationSet(ls);

    return (
        <LigationSampleContext.Provider value={ctxProps}>
            <Section>
                <Desc />
                <div className="ligation-sampler-body">
                    <LigationSamplerSwitches receiver={receiver} />
                    <PickerFrame
                        content={LigationSamplerInner}
                        defaultFontStyle={DefaultFontStyle}
                    />
                </div>
            </Section>
        </LigationSampleContext.Provider>
    );
}

function Desc() {
    return (
        <div className="section-introduction">
            <h2 className="header">Ligations for Coding</h2>
            <div className="content">
                <p>
                    Iosevka’s monospace families supports not only one <em>ligation set</em>, but
                    also <em>language-specific ligations</em>. Enable the corresponded OpenType
                    feature in your editor, and the correct ligation will appear.
                </p>
                <p>
                    Leveraging advanced OpenType techniques, Iosevka also supports{" "}
                    <em>long ligatures</em> like long arrows or horizontal bars built up with equal
                    signs.
                </p>
            </div>
        </div>
    );
}

type LigationSamplerSwitchesProps = {
    receiver: (ls: LigationSet) => void;
};
function LigationSamplerSwitches(props: LigationSamplerSwitchesProps) {
    const ctx = useContext(LigationSampleContext);
    return (
        <div className="ligation-set-switches">
            {ctx.availableLigations.map((ls, id) => (
                <LigationSamplerSwitch ligationSet={ls} receiver={props.receiver} key={id} />
            ))}
        </div>
    );
}

type LigationSamplerSwitchProps = {
    ligationSet: LigationSet;
    receiver: (ls: LigationSet) => void;
};
function LigationSamplerSwitch(props: LigationSamplerSwitchProps) {
    const ctx = useContext(LigationSampleContext);
    const fActive =
        ctx.currentLigationSet.tag === props.ligationSet.tag &&
        ctx.currentLigationSet.on === props.ligationSet.on;
    return (
        <a
            className={joinCls(
                "switch",
                fActive ? "active" : null,
                props.ligationSet.on ? "on" : "off"
            )}
            onClick={() => props.receiver(props.ligationSet)}
        >
            <span className="tag">{props.ligationSet.tag}</span>
            <span className="brief">{props.ligationSet.brief}</span>
        </a>
    );
}

function LigationSamplerInner(props: { fontStyle: FontStyle }) {
    const ctx = useContext(LigationSampleContext);

    const buildUpSet = new Set(ctx.currentLigationSet.buildUp);
    const finalFont: FontStyle = {
        ...props.fontStyle,
        ligationOn: ctx.currentLigationSet.on,
        ligationTag: ctx.currentLigationSet.tag
    };

    const items = LigationSamples.map((ls, index) => {
        const runs: React.ReactNode[] = [];
        for (let j = 0; j < ls.length; j++) {
            const sample = ls[j];
            if (j) runs.push(<span key={"gap " + j}> </span>);

            let rank = 0;
            for (let j = 0; j < sample.tags.length; j++) {
                if (buildUpSet.has(sample.tags[j])) rank = j + 1;
            }
            runs.push(
                <span key={"item " + j} className={"rank-" + rank}>
                    {sample.s}
                </span>
            );
        }
        return (
            <pre
                key={index}
                style={fontStyleToOtStyle(finalFont)}
                className={joinCls("sample-text", fontStyleToCls(finalFont))}
            >
                {runs}
            </pre>
        );
    });
    return <>{items}</>;
}
