import { useState, createContext, useContext } from "react";
import {
    FontStyle,
    StyleGrade,
    fontStyleToCls,
    SlantGrade,
    DefaultFontStyle,
    WidthGrade,
    WeightGrade
} from "../data-import/grades";
import { joinCls } from "../utils/join-classes";
import { SectionToolbar } from "./section";

type PickerFrameContextProps = {
    externFontStyle?: FontStyle;
    currentFontStyle: FontStyle;
    receiver: (fs: FontStyle) => void;
};
const PickerFrameCtx = createContext<PickerFrameContextProps>({
    externFontStyle: {},
    currentFontStyle: {},
    receiver: () => {}
});
export type PickerFrameProps = {
    externFontStyle?: FontStyle;
    defaultFontStyle?: FontStyle;
    wholeSection?: boolean;
    disableStyle?: boolean;
    disableWeight?: boolean;
    disableWidth?: boolean;
    disableSlant?: boolean;
    styleGrades?: StyleGrade[];
    onFontSet?: (fontStyle: FontStyle) => void;
    content: (props: { fontStyle: FontStyle }) => JSX.Element;
};
export function PickerFrame(props: PickerFrameProps) {
    const [fontStyle, setFontStyle] = useState<FontStyle>({ ...props.defaultFontStyle });
    const receiver = (fs: FontStyle) => {
        if (props.onFontSet) {
            props.onFontSet({ ...fontStyle, ...fs });
        }
        setFontStyle({ ...fontStyle, ...fs });
    };

    const styleGrades = props.styleGrades || [StyleGrade.Sans, StyleGrade.Slab];

    return (
        <PickerFrameCtx.Provider
            value={{
                externFontStyle: props.externFontStyle,
                currentFontStyle: fontStyle,
                receiver
            }}
        >
            <div className="picker-frame">
                <SectionToolbar withToolbar={props.wholeSection}>
                    <Group disabled={props.disableStyle}>
                        {styleGrades.map(grade => (
                            <Button apply={{ style: grade }} key={grade}>
                                {StyleGrade[grade]}
                            </Button>
                        ))}
                    </Group>
                    <Group disabled={props.disableWeight}>
                        <Button apply={{ weight: WeightGrade.Thin }}>1</Button>
                        <Button apply={{ weight: WeightGrade.ExtraLight }}>2</Button>
                        <Button apply={{ weight: WeightGrade.Light }}>3</Button>
                        <Button apply={{ weight: WeightGrade.Regular }}>4</Button>
                        <Button apply={{ weight: WeightGrade.Medium }}>5</Button>
                        <Button apply={{ weight: WeightGrade.SemiBold }}>6</Button>
                        <Button apply={{ weight: WeightGrade.Bold }}>7</Button>
                        <Button apply={{ weight: WeightGrade.ExtraBold }}>8</Button>
                        <Button apply={{ weight: WeightGrade.Heavy }}>9</Button>
                    </Group>
                    <Group disabled={props.disableSlant}>
                        <Button
                            unapply={{ slant: SlantGrade.Upright }}
                            apply={{ slant: SlantGrade.Italic }}
                        >
                            Italic
                        </Button>
                        <Button
                            unapply={{ slant: SlantGrade.Upright }}
                            apply={{ slant: SlantGrade.Oblique }}
                        >
                            Oblique
                        </Button>
                    </Group>
                    <Group disabled={props.disableWidth}>
                        <Button
                            unapply={{ width: WidthGrade.Normal }}
                            apply={{ width: WidthGrade.Expanded }}
                        >
                            Extended
                        </Button>
                    </Group>
                </SectionToolbar>
                <props.content fontStyle={{ ...props.externFontStyle, ...fontStyle }} />
            </div>
        </PickerFrameCtx.Provider>
    );
}
type PickerButtonGroupProps = {
    disabled?: boolean;
    children?: React.ReactNode;
};
function Group(props: PickerButtonGroupProps) {
    if (props.disabled) return null;
    else return <div className="button-group">{props.children}</div>;
}

type PickerButtonProps = {
    apply: FontStyle;
    unapply?: FontStyle;
    children?: React.ReactNode;
};
function Button(props: PickerButtonProps) {
    const ctx = useContext(PickerFrameCtx);
    let fActive = true;
    const currentStyle = { ...DefaultFontStyle, ...ctx.currentFontStyle };
    for (const prop in props.apply) {
        if ((currentStyle as any)[prop] !== (props.apply as any)[prop]) fActive = false;
    }
    return (
        <a
            onClick={() => ctx.receiver(fActive && props.unapply ? props.unapply : props.apply)}
            className={joinCls(
                "picker-button",
                fActive ? "active" : null,
                fontStyleToCls({ ...DefaultFontStyle, ...props.apply })
            )}
        >
            {props.children}
        </a>
    );
}
