import * as React from "react";
import { FontStyle, fontStyleToCls, fontStyleToOtStyle, SlantGrade } from "../data-import/grades";
import { joinCls } from "../utils/join-classes";

export type Token = { text: string; scopes: string[] };
export type TokenizedCodeProps = {
    fontStyle: FontStyle;
    code: Token[][];
    highlightCharSet?: string[];
    literature?: boolean;
};
export function TokenizedCode(props: TokenizedCodeProps) {
    const lines: JSX.Element[] = [];
    for (let index = 0; index < props.code.length; index++) {
        lines.push(
            <CodeLine
                codeLine={props.code[index]}
                highlightCharSet={props.highlightCharSet}
                key={index}
            />
        );
    }
    return (
        <pre
            className={joinCls(
                "sample-code",
                fontStyleToCls(props.fontStyle),
                props.literature ? "literature" : null
            )}
            style={fontStyleToOtStyle(props.fontStyle)}
        >
            {lines}
        </pre>
    );
}

type CodeLineProps = { codeLine: Token[]; highlightCharSet?: string[] };
function CodeLine(props: CodeLineProps) {
    const runs: JSX.Element[] = [];
    for (let index = 0; index < props.codeLine.length; index++) {
        runs.push(
            <CodeToken
                token={props.codeLine[index]}
                highlightCharSet={props.highlightCharSet}
                key={index}
            />
        );
    }
    if (!runs.length) {
        runs.push(<React.Fragment key={0}> </React.Fragment>);
    }
    return <div>{runs}</div>;
}

type CodeTokenProps = { token: Token; highlightCharSet?: string[] };
function CodeToken(props: CodeTokenProps) {
    return (
        <span className={joinCls("token", ...props.token.scopes)}>
            {props.highlightCharSet && props.highlightCharSet.length ? (
                <HighlightChars
                    text={props.token.text}
                    highlightCharSet={props.highlightCharSet}
                />
            ) : (
                props.token.text
            )}
        </span>
    );
}

type HighlightCharsProps = { text: string; highlightCharSet: string[] };
function HighlightChars(props: HighlightCharsProps) {
    if (!props.highlightCharSet.length) return <>{props.text}</>;
    const re = new RegExp("(" + props.highlightCharSet.map(escapeRegExp).join("|") + ")", "g");
    let subRuns: JSX.Element[] = [];
    let subRunStrings = props.text.split(re);
    for (let j = 0; j < subRunStrings.length; j++) {
        if (subRunStrings[j].match(re)) {
            subRuns.push(
                <span className="highlight" key={j}>
                    {subRunStrings[j]}
                </span>
            );
        } else {
            subRuns.push(<span key={j}>{subRunStrings[j]}</span>);
        }
    }
    return <>{subRuns}</>;
}

function escapeRegExp(s: string) {
    return s.replace(/[.*+?^${}()|[\]\\]/g, "\\$&"); // $& means the whole matched string
}
