import * as React from "react";
import { createContext, useContext, useState } from "react";
import { PickerFrame } from "../components/picker-frame";
import { Section } from "../components/section";
import { TokenizedCode } from "../components/tokenized-code";
import {
    DefaultFontStyle,
    FontStyle,
    SlantGrade,
    fontStyleToOtStyle,
    StyleGrade
} from "../data-import/grades";
import * as tokenizedSampleCode from "../tokenized-sample-code";
import { StylisticSets, CharacterVariants, CharacterVariantDefaults, Stats } from "../data-import";
import { joinCls } from "../utils/join-classes";

type StylisticSet = {
    tag: string;
    description: string;
    hotCharSetUpright: string[];
    hotCharSetItalic: string[];
    uprightComposition: string[];
    italicComposition: string[];
};
type CharVariant = {
    selector: string;
    tag: string;
    italicOnly: boolean;
};
type CharacterVariantChoice = {
    ss?: string;
    cv: string[];
};
type CharacterVariantSamplerContextProps = {
    currentChoice: CharacterVariantChoice;
    isSlab: boolean;
    isItalic: boolean;
    currentSelectors: string[];
    nonDefaultSelectors: string[];
    currentHotChars: string[];
};
const CharacterVariantSamplerContext = createContext<CharacterVariantSamplerContextProps>({
    currentChoice: { cv: [] },
    isSlab: false,
    isItalic: false,
    currentSelectors: [],
    nonDefaultSelectors: [],
    currentHotChars: []
});

export function CharacterVariantSampler() {
    const [isSlab, setIsSlab] = useState<boolean>(false);
    const [isItalic, setIsItalic] = useState<boolean>(false);
    const [currentChoice, setCurrentChoice] = useState<CharacterVariantChoice>({
        ss: "off",
        cv: []
    });
    const cr = choiceToSelectors(currentChoice, isSlab, isItalic);
    const ctxProps: CharacterVariantSamplerContextProps = {
        currentChoice,
        isSlab,
        isItalic,
        currentSelectors: cr.selectors,
        nonDefaultSelectors: cr.nonDefaultSelectors,
        currentHotChars: cr.hotChars
    };
    const receiver = (ls: CharacterVariantChoice) => setCurrentChoice(ls);
    const setFontHandler = (font: FontStyle) => {
        setIsSlab(font.style === StyleGrade.Slab);
        setIsItalic(font.slant === SlantGrade.Italic);
    };

    return (
        <CharacterVariantSamplerContext.Provider value={ctxProps}>
            <Section>
                <Desc />
                <div className="character-variant-sampler-body">
                    <SwitchPanel receiver={receiver} />
                    <PickerFrame
                        content={Inner}
                        defaultFontStyle={DefaultFontStyle}
                        onFontSet={setFontHandler}
                    />
                </div>
            </Section>
        </CharacterVariantSamplerContext.Provider>
    );
}

function Desc() {
    return (
        <div className="section-introduction">
            <h2 className="header">Variants à&nbsp;la&nbsp;Carte</h2>
            <div className="content">
                <p>
                    Monospace families in Iosevka provides{" "}
                    <em>{Stats.possibleCharacterVariants}</em> possible variants in total. There
                    are <em>{Stats.characterVariants}</em> character variant features and{" "}
                    <em>{Stats.stylisticSets}</em> stylistic sets. You can either select one
                    pre-defined stylistic set, or cherry-pick your flavor.
                </p>
            </div>
        </div>
    );
}

type SwitchPanelProps = {
    receiver: (ls: CharacterVariantChoice) => void;
};
function SwitchPanel(props: SwitchPanelProps) {
    return (
        <div className="switches">
            <SsPanel {...props} />
            <CvPanel {...props} />
            <TagShower />
        </div>
    );
}
function SsPanel(props: SwitchPanelProps) {
    return (
        <div className="ss">
            <h3>Stylistic Sets</h3>
            {StylisticSets.map(item => (
                <SsPanelOption stylisticSet={item} receiver={props.receiver} key={item.tag} />
            ))}
        </div>
    );
}

type SsPanelOptionProps = {
    receiver: (ls: CharacterVariantChoice) => void;
    stylisticSet: StylisticSet;
};
function SsPanelOption(props: SsPanelOptionProps) {
    const ss = props.stylisticSet;
    const ctx = useContext(CharacterVariantSamplerContext);

    const fActive = ctx.currentChoice.ss === ss.tag;
    return (
        <a
            className={joinCls("option", fActive ? "active" : null)}
            onClick={() => props.receiver({ ss: ss.tag, cv: [] })}
        >
            <span className="tag">{ss.tag}</span>
            <span className="brief">{ss.description}</span>
        </a>
    );
}

function CvPanel(props: SwitchPanelProps) {
    return (
        <div className="cv">
            <h3>Cherry-Picking</h3>
            <div className="options">
                {CharacterVariants.map((item, index) => (
                    <CvPanelGroup
                        receiver={props.receiver}
                        charVariants={item.configs.map(item => ({
                            tag: item.tag || item.tagItalic || "",
                            selector: item.selector,
                            italicOnly: !!item.tagItalic
                        }))}
                        groupSamplers={item.descSampleText}
                        isLigatureSampler={item.ligatureSampler}
                        key={index}
                    />
                ))}
            </div>
        </div>
    );
}

type CvPanelGroupProps = {
    receiver: (ls: CharacterVariantChoice) => void;
    charVariants: CharVariant[];
    groupSamplers: string[];
    isLigatureSampler: boolean;
};
function CvPanelGroup(props: CvPanelGroupProps) {
    const ctx = useContext(CharacterVariantSamplerContext);

    const makeChoice = function(tag: string, selector: string, enable: boolean) {
        const groupSelectorSet = new Set(props.charVariants.map(cv => cv.selector));
        const currentSelectors = choiceToSelectors(
            ctx.currentChoice,
            ctx.isSlab,
            ctx.isItalic
        ).selectors.filter(selector => !groupSelectorSet.has(selector));
        if (enable) currentSelectors.push(selector);
        props.receiver({ cv: selectorsToCvTags(currentSelectors) });
    };
    return (
        <div className="group">
            {props.charVariants.map(cv => (
                <CvPanelOption
                    receiver={makeChoice}
                    groupSamplers={props.groupSamplers}
                    isLigatureSampler={props.isLigatureSampler}
                    {...cv}
                    key={cv.selector}
                />
            ))}
        </div>
    );
}

type CvPanelOptionProps = CharVariant & {
    receiver: (tag: string, selector: string, enable: boolean) => void;
    groupSamplers: string[];
    isLigatureSampler: boolean;
};
function CvPanelOption(props: CvPanelOptionProps) {
    const ctx = useContext(CharacterVariantSamplerContext);

    let fActive = false;
    let fNonDefault = false;
    for (const item of ctx.currentSelectors) if (item === props.selector) fActive = true;
    for (const item of ctx.nonDefaultSelectors) if (item === props.selector) fNonDefault = true;

    return (
        <a
            className={joinCls(
                "option",
                fActive ? "active" : "inactive",
                fNonDefault ? "non-default" : null
            )}
            style={fontStyleToOtStyle({ ...DefaultFontStyle, charVarTags: [props.tag] })}
            onClick={() => props.receiver(props.tag, props.selector, true)}
            title={props.tag}
        >
            <span className={props.italicOnly ? "italic-sample" : "upright-sample"}>
                {props.groupSamplers[0]}
            </span>
        </a>
    );
}

function TagShower() {
    const ctx = useContext(CharacterVariantSamplerContext);
    const currentSelectors = choiceToSelectors(ctx.currentChoice, ctx.isSlab, ctx.isItalic);

    const charVarTags = ctx.currentChoice.ss
        ? ctx.currentChoice.ss === "off"
            ? []
            : [ctx.currentChoice.ss]
        : currentSelectors.nonDefaultTags;
    return (
        <div className="tag-shower">
            <h3>Current Configuration</h3>
            <pre className="body">
                {charVarTags
                    .sort()
                    .map(tag => `"${tag}"`)
                    .join(", ") || <span className="na">N/A</span>}
            </pre>
        </div>
    );
}

function Inner(props: { fontStyle: FontStyle }) {
    const ctx = useContext(CharacterVariantSamplerContext);
    const finalFont: FontStyle = {
        ...props.fontStyle,
        ligationTag: "XJS0",
        ligationOn: true,
        charVarTags: ctx.currentChoice.ss ? [ctx.currentChoice.ss] : ctx.currentChoice.cv
    };

    return (
        <>
            <TokenizedCode
                fontStyle={finalFont}
                code={tokenizedSampleCode.Alphabet}
                highlightCharSet={ctx.currentHotChars}
            />
            <TokenizedCode fontStyle={finalFont} code={tokenizedSampleCode.Lcs} />
        </>
    );
}

function choiceToSelectors(choice: CharacterVariantChoice, slab: boolean, italic: boolean) {
    let sUpright: string[] = [],
        sItalic: string[] = [];
    if (choice.ss) {
        for (const ss of StylisticSets) {
            if (choice.ss === ss.tag) {
                sUpright = ss.uprightComposition;
                sItalic = ss.italicComposition;
            }
        }
    } else {
        const cvSelectorSet = new Set(choice.cv);
        for (const gr of CharacterVariants) {
            for (const cv of gr.configs) {
                if (cvSelectorSet.has(cv.tag || cv.tagUpright || "")) sUpright.push(cv.selector);
                if (cvSelectorSet.has(cv.tag || cv.tagItalic || "")) sItalic.push(cv.selector);
            }
        }
    }
    const defaultSelectors = [
        ...CharacterVariantDefaults.default.design,
        ...(italic
            ? CharacterVariantDefaults.default.italic
            : CharacterVariantDefaults.default.upright),
        ...(slab ? CharacterVariantDefaults.slab.design : []),
        ...(slab
            ? italic
                ? CharacterVariantDefaults.slab.italic
                : CharacterVariantDefaults.slab.upright
            : [])
    ];
    const accumulatedSelectors = [...defaultSelectors, ...sUpright, ...sItalic];

    let hotChars: string[] = [];
    let selectors: string[] = [];
    let nonDefaultSelectors: string[] = [];
    let tags: string[] = [];
    let nonDefaultTags: string[] = [];
    for (const gr of CharacterVariants) {
        let groupDefaultResult = "";
        let groupResult = "";
        let groupTag = "";
        const selectorSet = new Set(gr.configs.map(cfg => cfg.selector));
        for (const selector of defaultSelectors) {
            if (selectorSet.has(selector)) groupDefaultResult = selector;
        }
        for (const selector of accumulatedSelectors) {
            if (selectorSet.has(selector)) groupResult = selector;
        }
        for (const cfg of gr.configs) {
            if (groupResult && cfg.selector === groupResult)
                groupTag = cfg.tag || cfg.tagItalic || cfg.tagUpright || "";
        }

        if (groupResult) {
            selectors.push(groupResult);
            tags.push(groupTag);
        }
        if (groupResult !== groupDefaultResult) {
            nonDefaultSelectors.push(groupResult);
            nonDefaultTags.push(groupTag);
            for (const ch of gr.descSampleText) hotChars.push(ch);
        }
    }
    return { selectors, nonDefaultSelectors, tags, nonDefaultTags, hotChars };
}

function selectorsToCvTags(selectors: string[]) {
    let tags: string[] = [];
    const selectorSet = new Set(selectors);
    for (const gr of CharacterVariants) {
        for (const cv of gr.configs) {
            if (selectorSet.has(cv.selector)) {
                tags.push(cv.tag || cv.tagUpright || cv.tagItalic || "");
            }
        }
    }
    return tags;
}
