import * as rawData from "./iosevka.json";

export const FontPackageVersion = rawData.version;
export const SupportedLanguages = rawData.languages;
export const CharacterVariants = rawData.variantsData.cvData;
export const StylisticSets = rawData.variantsData.ssData;
export const CharacterVariantDefaults = {
    default: rawData.variantsData.default,
    slab: rawData.variantsData.slabDefaultOverride
};

export const LigationSamples = rawData.ligationData.samplesNarrow;
export const RawLigationSets = rawData.ligationData.nonMergeSets;

export type LigationSet = { tag: string; on: boolean; buildUp: string[]; brief: string };
export const AvailableLigationSets: LigationSet[] = RawLigationSets.map(lig => ({
    tag: lig.tag,
    on: lig.switch === "off" ? false : true,
    buildUp: lig.buildup,
    brief: lig.brief
}));

export const Stats = {
    ...rawData.stats,
    supportedLanguages: SupportedLanguages.length,
    characterVariants: CharacterVariants.map(
        gr => gr.configs.filter(cv => cv.tag || cv.tagItalic || cv.tagUpright).length
    ).reduce((a, b) => a + b, 0),
    possibleCharacterVariants: CharacterVariants.map(
        gr => gr.configs.filter(cv => cv.tag || cv.tagItalic || cv.tagUpright).length
    ).reduce((a, b) => a * b, 1),
    stylisticSets: StylisticSets.filter(ss => ss.effective && ss.tag).length,
    ligationSets: AvailableLigationSets.filter(lig => lig.on).length,
    extraOtFeatures: 2,
    weightCount: 9,
    slopeCount: 3,
    widthCount: 2
};
